<template>
    <div id="app">
        <pp-header></pp-header>
        <pp-home></pp-home>
        <pp-footer></pp-footer>
    </div>
</template>

<script>
    import Header from './components/Header.vue'
    import Home from './components/Home.vue'
    import Footer from './components/Footer.vue'

    export default {
        components: {
            ppHeader: Header,
            ppHome: Home,
            ppFooter: Footer
        }
    }
</script>

<style>
    #app {
        font-family: 'Raleway', sans-serif !important;
    }
</style>



