<template>
	<headroom id="js-header" class="navbar__wrap">
		<div class="navbar">
			<div class="pull-left">
				<a href="#" class="navbar__categories"><img src="../assets/menu.svg" alt="Hamburger" class="icon-category"> <span class=" d-none d-sm-inline">Categorias</span></a>
			</div>
			<div class="pull-right">
				<a href="#" class="navbar__submit pl-lg-5 pr-lg-5 pl-3 pr-3"><img src="../assets/logoHM.svg" alt="Heroes Marvel" class=" navbar__logo"></a>
				<a target="_blank" rel="external" href="http://twitter.com/aeam" class="navbar__twitter">
					<span class="d-none d-sm-inline color-sigueme">S&iacute;gueme  </span> <span class="color-twitter">@aeam</span>
				</a>
			</div>
		</div>
	</headroom>
</template>

<script>
	import { headroom } from 'vue-headroom'

	export default {
  		components: {
    		headroom
  		}
	}
</script>

<style>
	.navbar__wrap {
    	position: fixed;
    	z-index: 10;
    	right: 0;
    	left: 0;
    	top: 0;
    	height: 135px;
	}
	.navbar {
    	position: relative;
    	height: 80px;
    	width: 100%;
    	background-color: #666666;
    	padding-right: 0;
	}
	.icon-category {
    	width: 35px;
    	height: 35px;
    	padding: 0.5em;
	}
	.navbar__submit {
    	float: right;
    	line-height: 80px;
    	font-size: 12px;
    	/*padding-left: 40px;
    	padding-right: 40px;*/
    	display: inline-block;
    	text-align: center;
    	font-weight: 700;
    	background-color: #abdb25;
    	color: #fff;
    	text-transform: uppercase;
    	text-decoration: none;
	}
	.navbar__twitter {
    	float: right;
    	line-height: 80px;
    	font-size: 12px;
    	padding-left: 30px;
    	padding-right: 30px;
    	display: inline-block;
    	text-align: center;
    	font-weight: 700;
    	text-decoration: none;
	}

	.navbar__twitter:hover {
    	text-decoration: none;
	}
	.navbar__logo {
    	width: 80px;
    	height: 80px;
    	z-index: 200;
    	display: block;
	}
	.navbar__categories {
    	/*padding-left: 30px;
    	padding-right: 30px;*/
    	display: inline-block;
    	text-align: center;
    	text-decoration: none;
    	text-transform: uppercase;
    	color: white;
    	font-size: 12px;
	}
	.color-twitter {
		color: #abdb25;
	}
	.color-sigueme {
		color: white;
	}
</style>


