<template>
	<div class="container-fluid">
		<div class="titulo mt-5 pt-5">
			<h1 class="text-center mt-4">Heroes de <span class="color_marvel">Marvel</span></h1>
			<h4 class="text-center">Vota por tu <span class="color_marvel">Evento</span> favorito</h4>
		</div>
		<div class="contenido mt-5 mb-5">
			<div class="row" v-if="characters && characters.length">
				<div class="col-xl-3 col-lg-4 col-md-6 pb-4" v-for="character of characters">
					<div class="cont_info">
						<div class="header_info">
							<img :src="character.thumbnail.path+'.jpg'" alt="Super heroe" class="img-fluid rounded tamano_img_heroe">
						</div>
						<div class="medio_info pl-2 pr-2 pt-2 pl-md-3 pr-md-3 pt-md-3">
							<h5 class="nom_sup">{{ character.name }}</h5>
							<div v-if="character.description">
								<p class="text-truncate">{{ character.description}}</p>
							</div>
							<div v-else>
								<p class="text-truncate">Descripci&oacute;n del superh&eacute;roe no encontrada consulte luego...</p>
							</div>
						</div>
						<div class=" pl-2 pr-2 pl-md-3 pr-md-3">
							<hr>
						</div>
						<div class="footer_info pl-2 pr-2 pl-md-3 pr-md-3">
							<div class="row">
								<div class="col-6">
									<a href="#" class="hashsuper">#superheroe</a>
								</div>
								<div class="col-6">
									<p class="text-right likes_num">&#9733; {{visitCount}}</p>
								</div>
							</div>
						</div>
						<div class="star_votar">
							<input class="star" type="checkbox" title="bookmark page" checked>
						</div>
					</div>
				</div>
				<!--<div class="col-xl-4 col-md-6"></div>
				<div class="col-xl-4 col-md-6"></div>-->
			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'

	export default {
		data: () => ({
			characters: [],
			errors: [],
			visitCount: 0
		}),

		created() {
			axios.get('http://gateway.marvel.com/v1/public/events', {
				params: {
					ts: 1,
					apikey: '68d5fdca89cb7931bbe08b90adce4517',
					hash: '73811f1dc75ddf097ec7de10d3dfb0b2'
				}
			})
				.then(response => {
					this.characters = response.data.data.results;
					//this.characters = response.data;
					//console.log(response.data.data.results)
				})
				.catch(e => {
					this.errors.push(e);
					//console.log(e)
				})
		}
	}
</script>

<style>
	.titulo {
		color: #666666;
	}

	.color_marvel {
		color: #abdb25;
	}

	.cont_info {
		border: 3px solid #abdb25;
		border-radius: 8px;
	}

	.cont_info:hover {
		border: 0;
	}

	.medio_info p {
		color: #666666;
	}

	.likes_num {
		color: #abdb25;
	}

	.hashsuper {
		color: black;
	}

	.hashsuper:hover {
		color: #abdb25;
	}

	.star_votar {
		position: absolute;
		top: 45%;
		right: 5em;
	}

	.star {
    	visibility:hidden;
    	font-size:40px;
    	cursor:pointer;
    	color: white;
	}
	.star:before {
   		content: "\2605";
   		position: absolute;
   		visibility:visible;
   		background-color: #abdb25;
   		border-radius: 100px;
   		padding-right: 14px;
   		padding-left: 14px;
	}
	.star:checked:before {
   		content: "\2606";
   		position: absolute;
	}

</style>


